@import url("https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lexend+Deca:wght@500;800&display=swap");

* {
  margin: 0px;
  padding: 0px;
}
body {
  font-family: "Noto Sans", sans-serif;
  margin: 0px 8px;
  height: auto;
  width: auto;
}
/* for Header:    font-family: 'Lexend Deca', sans-serif; */

/* NavBar */
.NavBar nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 2px;
  font-size: 1.5rem;
}
.NavBar nav ul {
  display: flex;
  align-items: center;
}
.NavBar nav ul li {
  margin: 0px 10px;
  list-style: none;
  color: rgb(94, 94, 94);
}
.NavBar nav ul li:hover {
  font-weight: bold;
}
#Last {
  background-color: rgb(50, 118, 255);
  color: white;
  margin-top: 0px 0px;
  position: relative;
  padding: 20px 15px;
}

.SearchBar {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  height: 140px;
}
.SearchBar ::placeholder {
  color: rgb(50, 118, 255);
}
.search {
  display: flex;
  align-items: flex-end;
}
.SearchBar input {
  border: 2px solid rgb(50, 118, 255);
  font-size: 1.8rem;
  margin-right: 10px;
  padding: 15px 5px;
  padding-right: 80px;
}
.ball1 {
  display: flex;
  align-items: center;
  margin-left: 50px;
}
.Onlyballs {
  display: flex;
  justify-content: space-around;
  margin-top: 15px;
}

/* Main  */
.Main {
  display: flex;
}
/* .left {
  
} */
.left h1 {
  font-size: 3.5rem;
  font-weight: bolder;
  font-family: "Lexend Deca", sans-serif;
  margin: 15px 0px;
}
.left h1 span {
  text-decoration: underline;
  text-decoration-color: rgb(255, 169, 9);
}
.left p {
  font-size: 1.9rem;
  font-weight: bold;
  color: rgb(103, 103, 103);
}
.span {
  color: rgb(50, 118, 255);
  font-size: 2rem;
}
.left button {
  margin-top: 8px;
  font-size: 1.6rem;
  padding: 18px 10px;
  color: rgb(50, 118, 255);
  background-color: white;
  border: 2px solid rgb(50, 118, 255);
}
.left button:hover{
  background-color: rgb(50, 118, 255);
  color: white;
}
#leftbtn{
  border-right: 0px;
}
#rightbyn{
  border-left: 0px;
}

.right {
  display: flex;
}
.right img {
  height: 600px;
  max-width: 800px;
}

.below{
  display: flex;
  justify-content: center;
  color: rgb(50, 118, 255);
  margin: 50px 0px;
}
.below h2{
  word-spacing: 5px;
}
.below img{
  margin-left: 15px;
  padding: 5px 6px;
  border-radius: 50%;
  border: 2px solid rgb(50, 118, 255);
}

/* Mission */
.outermission{
  display: flex;
  align-items: center;
  height: 400px;
}
.Mission{
  margin: auto;
  border: 4px soild black;
}
.Missionleft, .Missionright{
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: bold;
}
.Missionleft h1 span{
  text-decoration: underline;
  text-decoration-color: orange;
  margin-bottom: -50px;
}
.Missionleft h1{
  padding-left: 145px;
  font-family: 'Lexend Deca', sans-serif;
}
.Missionleftimg{
  position: relative;
  margin-left: -205px;
}
#outerMissionimg{
  height: 300px;
  width: 200px;
  margin-left: -130px;
}
.Missionright p{
  margin-top: 15px;
  color: rgb(125, 124, 124);
}

/* Companies */
.Companies{
  display: flex;
  justify-content:space-between ;
}
.one img{
  padding: 5px 0px;
  height: 50px;
}
.one h1{
  padding: 5px 0px;
}
.one p{
  font-size: 1.4rem;
  color: rgb(113, 113, 113);
  padding-bottom: 20px;
}
.one span{
  font-size: 1.6rem;
  color: orange;
}
.companyBall{
  margin-top: 40px;
  display: flex;
  justify-content: space-around;
}
.comanyRight{
  margin-right: 250px;
}

/* Cards */
.CardsOuter{
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
}
.card h1, .card p{
  padding: 0px 60px;
}
.card img{
  height: 280px;
  padding: 20px 40px;
}
.card{
  border: 2px solid rgb(125, 125, 125);
  border-radius: 20px;
  height: 700px;
  max-width: 500px;
  padding: 10px 20px;
  font-size: 1.2rem;
}
.cardSpan{
  display: flex;
  align-items: center;
  padding-left: 58px;
  padding-top: 40px;
}
.cardSpan img{
  padding: 0px;
  height: 20px;
}
.cardSpan h2{
  color: rgb(50, 118, 255);
}
.cardLast{
  border: 2px solid rgb(125, 125, 125);
  border-radius: 20px;
  height: 600px;
  max-width: 500px;
  padding: 10px 20px;
  font-size: 1.2rem;
}
.cardContentLeft{
  padding-left: 40px;
}
.cardContentRight{
  padding-right: 40px;
}
.cardBalls{
  padding: 100px 0px;
  display: flex;
  justify-content: space-between;
}
.cardBall{
  padding: 90px;
  display: flex;
  justify-content: center;
}
.cardContent{
  padding-bottom: 180px;
}
.cardContent span{
  color: rgb(50, 118, 255);
  font-size: 1.6rem;
}
.cardContentLast{
  padding-top: 190px;
}
.cardLast h1, .cardLast p{
  padding: 0px 40px;
}
.cardLastContent{
  display: flex;
  padding-top: 30px;
  color: rgb(50, 118, 255);
}
.cardLastContent img{
  padding-left: 20px;
}

.Middle{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 100px 0px;
}
.Middle h1{
  font-size: 2.5rem;
  font-family: 'Lexend Deca', sans-serif;
  line-height: 70px;
}
.Middle span{
  text-decoration: underline;
  text-decoration-color: orange;
  text-decoration-thickness: 8px;
  text-underline-offset: 13px;
}

/* Plans  */
.Plans{
  display: flex;
  justify-content: space-evenly;
}
.planCard{
  color: rgb(50, 118, 255);
  height: 46rem;
  width: 25rem;
}
.planCard:hover{
  background-color: rgb(50, 118, 255);
  color: white;
  border-radius: 5px;
}
.planCard button{
  width:18.5rem;
  height: 4.0rem;
  font-size: 1.4rem;
  background-color: white;
  color: rgb(50, 118, 255);
  border: none;
  outline: 2px solid rgb(50, 118, 255);
  border-radius: 2px;
  margin-top: 20px;
  font-weight: 600;
  margin-left: 25px;
  padding-left: 0px;
}
.planCard ul li{
  display: flex;
  align-items: center;
}
.planCard ul li img{
  padding-right: 6px;
}
.price{
  display: flex;
  align-items: flex-end;
}
.price h1{
  font-family: 'Lexend Deca', sans-serif;
}
.price span{
  color: rgb(121, 119, 119);
}
.allList, .price, .list{
  padding: 10px 0px;
  padding-left: 20px;
}

.planAfter{
  display: flex;
  justify-content: space-between;
}
#small{
  max-height: 30px;
  max-width: 50px;
  overflow: hidden;
  margin-left: 100px;
}
#big{
  max-height: 80px;
  max-width: 80px;
  overflow: hidden;
  margin-top: 10px;
  margin-right: 700px;
}
#arc{
  max-height: 600px;
  max-width: 500px;
  overflow: hidden;
  margin-top: -150px;
  margin-right: -250px;
}

/* About  */
.AboutOuter{
  display: flex;
  justify-content: space-evenly;
}
.AboutDetails{
  display: flex;
  align-items: center;
  border-left: 6px solid rgb(50, 118, 255);
  font-size: 1.6rem;
}
.AboutCard, .AboutDetails, .AboutCardUser{
padding: 30px;
}
.AboutCard{
  display: flex;
  align-items: center;
}
.AboutCard h3 span{
  color: rgb(50, 118, 255);
}
.AboutCard img{
  border: 4px solid rgb(50, 118, 255);
  padding: 10px;
  border-radius: 5px;
  margin-right: 10px;
}

.AboutCardUser{
  display: flex;
}
.AboutCardUser img{
  border: 4px solid rgb(50, 118, 255);
  border-radius: 50%;
  padding: 10px;
  margin-right: 10px;
}
.AboutCardUser p b, .AboutOuter p h3{
  color: black;
}
.AboutCardUser p b{
  font-weight: bold;
  color: black;
}
.AboutCardUser p{
  color: rgb(78, 77, 77);
  font-weight: lighter;
  font-size: 20px;
}
.aboutimg{
  border-bottom: 4px solid rgb(50, 118, 255);
  margin-bottom: 20px;
  margin-right: 50px;
}

.right{
  display: block;
}
.Aboutlists{
  margin: 20px 30px;
  font-size: 20px;
}
.Aboutlists span{
  font-weight: bold;
  color: black;
}
.Aboutlists ul li {
  color: rgb(84, 84, 84);
  list-style: none;
  margin-top: 5px;
}
.Email{
  display: flex;
  margin: 20px 30px;
}
.Aboutright img{
  height: 40px;
  width: 43px;
  padding: 5px 10px;
}
.Email img{
  height: 60px;
  width: 50px;
  padding: 0px;
}
.Email input{
  background-color: rgb(224, 224, 224);
  border: none;
  width: 280px;
  height: 60px;
  font-size: 1.4rem;
}
.footer{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px 0px ;
}
.footer p{
  font-size: 1.3rem;
  color: rgb(123, 123, 123);
}
